                    <commonInput :value.sync="shouquanmaName" width="80"></commonInput>
                </div>
                <div class="update_status_content width_set fl">
                    <p class="condition_title"></p>
                    <div class="select_width">
                        <commonSelect
                            :list="shouquanmaStatusList"
