                        <div v-show="ele.operate.length">
                            <span
                                v-for="(item,key) in ele.operate"
                                :class="['cursor',{add_margin:key==0&&ele.operate.length>1,fuzhi:item=='0',chexiao:item=='1',yichexiao:item=='2'}]"
                                :key="key"
                                @click="tiqumaOperate(ele,item)"
