                    class="cursor"
                    @click="$emit('update:buyShangchuanma',false)"
                />
            </div>
            <div class="colorBg">
                <div class="account">
                    <div>:1131</div>
                </div>
                <div class="tuiguang_shangchuanma">
                    <p class="tuiguang_title"></p>
                    <ul class="maList clearfix">
                        <li v-for="(ele,index) in tuiguangShangchuanma" :key="index" class="fl">
                            <div
                                :class="['msg_wrap','cursor',{active:ele.active}]"
                                @click="selectTuiguangshangchuan(index)"
                            >
                                <div class="average_price">
                                    <p class="price">
                                        
                                        <span>{{ele.codeExercisePrice}}</span>/
                                    </p>
                                    <p class="tuiguang_content">{{ele.codeNumber}}</p>
                                </div>
                                <div class="price_wrap">
                                    <p class="huaxian">{{ele.codeOriginalMoney}}</p>
                                    <p class="total_price">{{ele.codeExerciseMoney}}</p>
                                </div>
                            </div>
                        </li>
                    </ul>
                    <p class="tuiguang_title"></p>
                    <ul class="maList clearfix">
                        <li v-for="(ele,index) in wannengShangchuanma" :key="index" class="fl">
                            <div
                                :class="['msg_wrap','cursor',{active:ele.active}]"
                                @click="selectWannengshangchuan(index)"
                            >
                                <div class="average_price">
                                    <p class="price">
                                        
                                        <span>{{ele.codeExercisePrice}}</span>/
                                    </p>
                                    <p class="tuiguang_content">{{ele.codeNumber}}</p>
                                </div>
                                <div class="price_wrap">
                                    <p class="huaxian">{{ele.codeOriginalMoney}}</p>
                                    <p class="total_price">{{ele.codeExerciseMoney}}</p>
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="tuiguang_shangchuanma no_border">
                    <p class="tuiguang_title add_top_margin"></p>
                    <ul class="zhifu_type clearfix">
                        <li
                            v-for="(ele,index) in zhifuType"
                            :key="index"
                            class="fl"
                            @click="selectZhifuType(index)"
                        >
                            <div :class="['cursor',{active:ele.active}]">{{ele.name}}</div>
                        </li>
                    </ul>
                    <div v-html="zhifuTypeErweima"></div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { setCookie, getCookie, delCookie } from "../assets/js/cookie";
export default {
    props: ["data", "buyShangchuanma"],
    data() {
        return {
            shouquanmaremark: "",
            // 
            totalPage: null,
            // 
            zhifuType: [
                {
                    name: "",
                    id: "0",
                    active: false
                }
                // {
                //     name: "",
                //     id: "1",
                //     active: false
                // }
            ],
            // 
            bugShangchuanma: true,
            // 
            tuiguangShangchuanma: [],
            wannengShangchuanma: [],
            shouquanmaName: "",
            shouquanmaUserName: "",
            // 
            // 
            shouquanmaTipList: [
                {
                    name: "",
                    id: "0"
                },
                {
                    name: "",
                    id: "1"
                },
                {
                    name: "",
                    id: "2"
                }
            ],
            shouquanmaNumber: "",
            shouquanmaRemark: "",
            shouquanmaSelected: "",
            // 
            shouquanmaMoldMsg: {},
            // 
            shouquanmaList: [],
            // 
            showShouquanma: false,
            // 
            tiquMoldMsg: {},
            // 
            showTiquma: false,
            // 
            tiqumaList: [],
            // 
            tiqumaName: "",
            tiqumaUserName: "",
            tiqumaStatusList: [
                {
                    name: "",
                    id: ""
                },
                {
                    name: "",
                    id: "0"
                },
                {
                    name: "",
                    id: "1"
                }
            ],
            tiqumaSelected: "",
            shouquanmaStatusList: [
                {
                    name: "",
                    id: ""
                },
                {
                    name: "",
                    id: "0"
                },
                {
                    name: "",
                    id: "1"
                },
                {
                    name: "",
                    id: "2"
                }
            ],
            shouquanmaSelectedRemark: "",
            // 
            totalCount: 10,
            countList: [
                {
                    name: 5,
                    id: 1
                },
                {
                    name: 10,
                    id: 2
                },
                {
                    name: 15,
                    id: 3
                },
                {
                    name: 20,
                    id: 4
                }
            ],
            countPage: 10,
            // 
            totalUpdate: 5,
            // 
            successUpdate: 5,
            // 
            shenheUpdate: 5,
            // 
            failUpdate: 5,
            // 
            grounding: 5,
            // 
            ungrounding: 5,
            // 
            publics: 5,
            // 
            tiqumaCount: 5,
            // 
            shouquanCount: 5,
            // 
            huizongData: {},
            // 
            numberSearch: "",
            // 
            selectedStausSearch: null,
            // 
            nameSearch: "",
            // 
            selectedTypeSearch: null,
            // ipa
            selectedipaStatusSearch: "",
            // 
            selectedEncodeSearch: "",
            updateStatusSearch: [
                {
                    name: "",
                    id: ""
                },
                {
                    name: "",
                    id: 2
                },
                {
                    name: "",
                    id: 0
                },
                {
                    name: "",
                    id: 1
                }
            ],
            updateTypeSearch: [
                {
                    name: "",
                    id: 0
                },
                {
                    name: "",
                    id: 1
                },
                {
                    name: "iTunes",
                    id: 2
                },
                {
                    name: "",
                    id: 3
                }
            ],
            ipaStatusSearch: [
                {
                    name: "",
                    id: 0
                },
                {
                    name: "",
                    id: 1
                },
                {
                    name: "",
                    id: 2
                }
            ],
            encodeSearch: [
                {
                    name: "",
                    id: ""
                },
                {
                    name: "",
                    id: 0
                },
                {
                    name: "",
                    id: 1
                },
                {
                    name: "",
                    id: 2
                }
            ],
            dataEdit: [],
            defaultPage: 1,
            defaultPagecount: 10,
            defaultShouquanPage: 1,
            defaultTiquPage: 1,
            // 
            tmpEle: null,
            tiquPageTotal: 1,
            shouquanPageTotal: 1,
            zhifuTypeErweima: "",
            describes: "",
            sccode: ""
        };
    },
    methods: {
        // 
        getData(page, pagecount) {
            this.dataEdit = [];
            this.$axios
                .post(
                    "https://www.ddipa.com:9090/DDApi/template/queryIPA",
                    this.$qs.stringify({
                        page: this.defaultPage,
                        id: "1",
                        pagecount: this.defaultPagecount,
                        uploadcode: this.numberSearch,
                        state: this.selectedStausSearch
                            ? this.selectedStausSearch.id
                                ? this.selectedStausSearch.id == 2
                                    ? "2,3"
                                    : this.selectedStausSearch.id
                                : this.selectedStausSearch.id
                            : null,
                        name: this.nameSearch,
                        updateType: this.selectedTypeSearch
                            ? this.selectedTypeSearch.id
                            : null,
                        encryption: this.selectedEncodeSearch
                            ? this.selectedEncodeSearch.id
                            : null,
                        describes: this.describes
                    })
                )
                .then(res => {
                    if (res.status == 200) {
                        res.data.data.forEach(ele => {
                            ele.operate = [];
                            if (ele.state == "0" || ele.state == "1") {
                                ele.operate = [];
                            } else if (ele.state == "2") {
                                ele.operate = ["1", "4"];
                            } else if (ele.state == "3") {
                                ele.operate = ["2", "4"];
                            } else if (ele.state == "4") {
                                ele.operate = ["3"];
                            }
                        });
                        this.dataEdit = res.data.data;
                        this.huizongData = res.data.allmessage[0];
                        this.totalPage = res.data.allmessage[0].allup;
                    }
                });
        },
        // 
        selectZhifuType(index) {
            this.zhifuType.forEach((ele, key) => {
                if (key == index) {
                    ele.active = true;
                } else {
                    ele.active = false;
                }
            });
            this.$async.auto({
                order: cb => {
                    var orderNumber = null;
                    var codeId = null;
                    this.wannengShangchuanma.forEach((ele, key) => {
                        if (ele.active) {
                            //////console.log(ele);
                            orderNumber = ele.codeNumber;
                            codeId = ele.codeId;
                        }
                    });
                    this.tuiguangShangchuanma.forEach((ele, key) => {
                        if (ele.active) {
                            //////console.log(ele);
                            orderNumber = ele.codeNumber;
                            codeId = ele.codeId;
                        }
                    });
                    var data = {
                        userId: getCookie('userid'),
                        money: this.codeExercisePrice,
                        orderNumber,
                        orderType: "1",
                        codeId
                    };
                    this.$axios
                        .post(
                            "https://www.ddipa.com:9090/DDApi/order/add/orders",
                            data
                        )
                        .then(res => {
                            if (res.status == 200) {
                                cb(null, res.data);
                            }
                        });
                },
                payOrder: [
                    "order",
                    res => {
                        var orderNumber = null;
                        var codeId = null;
                        this.wannengShangchuanma.forEach((ele, key) => {
                            if (ele.active) {
                                orderNumber = ele.codeNumber;
                                codeId = ele.codeId;
                            }
                        });
                        this.tuiguangShangchuanma.forEach((ele, key) => {
                            if (ele.active) {
                                orderNumber = ele.codeNumber;
                                codeId = ele.codeId;
                            }
                        });
                        var data = res.order;
                        data.zflx = "alipay";
                        this.$axios
                            .post(
                                "https://www.ddipa.com:9090/DDApi/pay/payOrder",
                                data
                            )
                            .then(res => {
                                if (res.status == 200) {
                                    var url, htmls;

                                    this.zhifuTypeErweima = res.data.message;
                                    let routerData = this.$router.resolve({
                                        path: "/applyText",
                                        query: { htmls: res.data.message }
                                    });
                                    url = res.data.message; //

                                    window.open(routerData.href, "_ blank");
                                    const div = document.createElement("div");
                                    div.innerHTML = htmls;
                                    document.body.appendChild(div);
                                    // document.forms[0].submit();
                                    this.buyShangchuanma = false;
                                    this.$confirm("", "", {
                                        confirmButtonText: "",
                                        cancelButtonText: "",
                                        type: "warning"
                                    })
                                        .then(() => {
                                            this.$router.go(0);
                                        })
                                        .catch(() => {
                                            this.$router.go(0);
                                        });
                                }
                            });
                    }
                ]
            });
        },
        // 
        selectTuiguangshangchuan(index) {
            this.tuiguangShangchuanma.forEach((ele, key) => {
                if (key == index) {
                    ele.active = true;
                    this.codeExercisePrice = ele.codeExercisePrice;
                } else {
                    ele.active = false;
                }
            });
            this.wannengShangchuanma.forEach((ele, key) => {
                ele.active = false;
            });
            this.zhifuType.forEach((ele, key) => {
                ele.active = false;
            });
        },
        // 
        selectWannengshangchuan(index) {
            this.wannengShangchuanma.forEach((ele, key) => {
                if (key == index) {
                    ele.active = true;
                    this.codeExercisePrice = ele.codeExercisePrice;
                } else {
                    ele.active = false;
                }
            });
            this.tuiguangShangchuanma.forEach((ele, key) => {
                ele.active = false;
            });
            this.zhifuType.forEach((ele, key) => {
                ele.active = false;
            });
        },
        handleSizeChange(val) {
            this.defaultPagecount = val;
            this.getData(this.defaultPage, this.defaultPagecount);
        },
        handleCurrentChange(val) {
            this.defaultPage = val;
            this.getData(this.defaultPage, this.defaultPagecount);
        },
        // 
        handleShouquanCurrentChange(val) {
            this.defaultShouquanPage = val;
            this.getShouquanma(
                this.tmpEle.id,
                this.tmpEle.uploadcode,
                this.defaultShouquanPage
            );
        },
        // 
        handleCurrentTiqumaChange(val) {
            this.defaultTiquPage = val;
            this.getTiquma(
                this.tmpEle.id,
                this.tmpEle.code,
                this.defaultTiquPage
            );
        },
        // 
        updatStatus(status) {
            var that = this;
            switch (status) {
                case "0":
                    return "";
                    break;
                case "1":
                    return "";
                    break;
                case "2":
                    return "";
                    break;
                case "3":
                    return "";
                    break;
                default:
                    "";
                    break;
            }
        },
        // ipa
        ipaStatus(status) {
            switch (status) {
                case "3":
                    return "";
                    break;
                case "2":
                    return "";
                    break;
                case "1":
                    return "";
                    break;
                case "0":
                    return "";
                    break;
            }
        },
        // 
        encodeType(status) {
            switch (status) {
                case "0":
                    return "";
                    break;
                case "1":
                    return "";
                    break;
                case "2":
                    return "";
                    break;
                default:
                    "";
                    break;
            }
        },
        // 
        operateStatus(status) {
            switch (status) {
                case "1":
                    return "";
                    break;
                case "2":
                    return "";
                    break;
                case "3":
                    return "";
                    break;
                case "4":
                    return "";
                    break;
                default:
                    "";
                    break;
            }
        },
        tiqumaOperateStatus(status) {
            switch (status) {
                case "0":
                    return "";
                    break;
                case "1":
                    return "";
                    break;
                case "2":
                    return "";
                    break;
                default:
                    "";
                    break;
            }
        },
        // 
        shouBtn(status) {
            switch (status) {
                case "1":
                    return "";
                    break;
                case "2":
                    return "";
                    break;
                default:
                    "";
                    break;
            }
        },
        // 
        time(time = +new Date()) {
            var date = new Date(time + 8 * 3600 * 1000); // 8
            return date
                .toJSON()
                .substr(0, 19)
                .replace("T", " ");
        },
        // 
        getUploadTime(date) {
            return this.time(date);
        },
        // 
        tiqumaStatus(status) {
            switch (status) {
                case "0":
                    return "";
                    break;
                case "1":
                    return "";
                    break;
                default:
                    "";
                    break;
            }
        },
        shouquanmaStatus(status) {
            switch (status) {
                case "0":
                    return "";
                    break;
                case "1":
                    return "";
                    break;
                case "2":
                    return "";
                    break;
                default:
                    "";
                    break;
            }
        },
        // 
        getTiquma(id, code, defaultTiquPage) {
            var that = this;
            // 
            this.$axios
                .post(
                    "https://www.ddipa.com:9090/DDApi/template/querytqcode",
                    this.$qs.stringify({
                        id: id,
                        code: code,
                        state: this.tiqumaSelected.id,
                        page: defaultTiquPage,
                        code: this.tiqumaName,
                        name: this.tiqumaUserName
                    })
                )
                .then(res => {
                    if (res.status == 200) {
                        //////console.log("res----");
                        //////console.log(res);
                        res.data.data.forEach(ele => {
                            if (ele.state == "0") {
                                ele.operate = ["0", "1"];
                            } else {
                                ele.operate = ["0", "2"];
                            }
                        });
                        that.tiqumaList = res.data.data;
                        that.tiquPageTotal = res.data.allmessage;
                    }
                });
        },
        // 
        getShouquanma(id, code, page) {
            var that = this;
            //   
            this.$axios
                .post(
                    "https://www.ddipa.com:9090/DDApi/template/querySqcode",
                    this.$qs.stringify({
                        id: id,
                        code: code,
                        page: page,
                        name: this.shouquanmaUserName,
                        sqcode: this.shouquanmaName,
                        state: this.shouquanmaSelected.id,
                        remarks: this.shouquanmaSelectedRemark.id
                    })
                )
                .then(res => {
                    if (res.status == 200) {
                        res.data.data.forEach(ele => {
                            if (ele.state == "0") {
                                ele.operate = ["0", "1"];
                            } else {
                                ele.operate = ["0", "2"];
                            }
                        });
                        that.shouquanmaList = res.data.data;
                        that.shouquanPageTotal = res.data.allmessage;
                    }
                });
        },
        // 
        showMa(ele, name, state, status) {
            var that = this;
            this.tmpEle = ele;
            if (status == "1") {
                this.showTiquma = true;
                this.showShouquanma = false;
                this.tiquMoldMsg = {
                    name,
                    ipaStatus: this.ipaStatus(state)
                };
                this.getTiquma(ele.id, ele.code, this.defaultTiquPage);
            } else {
                this.showShouquanma = true;
                this.showTiquma = false;
                this.shouquanmaMoldMsg = {
                    name,
                    ipaStatus: this.ipaStatus(state)
                };
                this.getShouquanma(
                    ele.id,
                    ele.uploadcode,
                    this.defaultShouquanPage
                );
            }
        },
        // ,
        tiqumaOperate(ele, item) {
            if (item == "0") {
                var ele = document.getElementById(
                    ele.code ? ele.code + ele.id : ele.sqcode + ele.id
                );
                ele.select(); // 
                document.execCommand("Copy"); // 
                alert("");
            } else if (item == "1") {
                if (ele.sqcode) {
                    this.$axios
                        .post(
                            "https://www.ddipa.com:9090/DDApi/updateTemplate/updateSqcode",
                            this.$qs.stringify({ id: ele.id, state: "2" })
                        )
                        .then(res => {
                            this.getShouquanma(
                                this.tmpEle.id,
                                this.tmpEle.uploadcode,
                                this.defaultShouquanPage
                            );
                        });
                } else {
                    this.$axios
                        .post(
                            "https://www.ddipa.com:9090/DDApi/updateTemplate/updatetqcode",
                            this.$qs.stringify({ id: ele.id, state: "1" })
                        )
                        .then(res => {
                            //////console.log("res++++");
                            //////console.log(res);
                            this.getTiquma(
                                this.tmpEle.id,
                                this.tmpEle.code,
                                this.defaultTiquPage
                            );
                        });
                }
            }
        },
        // ipa    
        ipaOperate(item, ele) {
            var obj = {};
            for (var i in ele) {
                obj[i] = ele[i];
            }
            delete obj.operate;
            //////console.log(obj);
            if (item == "4") {
                this.$router.push(
                    "upload?id=" + ele.id + "&uploadcode=" + ele.uploadcode
                );
            } else if (item == "3") {
                //////console.log("");
                this.$router.push("upload?uploadcode=" + ele.uploadcode);
            } else if (item == "2") {
                //////console.log("");
                this.$axios
                    .post(
                        "https://www.ddipa.com:9090/DDApi/updateTemplate/updateIPA",
                        this.$qs.stringify({ id: ele.id, state: 2 })
                    )
                    .then(res => {
                        if (res.status == 200) {
                            //////console.log("res.data+++");
                            //////console.log(res.data);
                            this.defaultPage = 1;
                            this.defaultPagecount = 10;
                            this.getData(
                                this.defaultPage,
                                this.defaultPagecount
                            );
                        }
                    });
            } else if (item == "1") {
                //////console.log("");
                this.$axios
                    .post(
                        "https://www.ddipa.com:9090/DDApi/updateTemplate/updateIPA",
                        this.$qs.stringify({ id: ele.id, state: 3 })
                    )
                    .then(res => {
                        if (res.status == 200) {
                            //////console.log("res.data+++");
                            //////console.log(res.data);
                            this.defaultPage = 1;
                            this.defaultPagecount = 10;
                            this.getData(
                                this.defaultPage,
                                this.defaultPagecount
                            );
                        }
                    });
            }
        },
        compare(property) {
            return function(a, b) {
                var value1 = a[property];
                var value2 = b[property];
                return value1 - value2;
            };
        },
        shengchengShouquanma() {
            this.$axios
                .post(
                    "https://www.ddipa.com:9090/DDApi/insetTemplate/insertSqcode",
