<template>
    <div class="upload_container">
        <NAV :activeIndex="2"></NAV>
        <div class="upload_title_container">
            <div class="upload_title">
                <p class="upload_menu">IPA / IPA</p>
                <p class="upload_des">
                    IPA
                    <span>*</span>
                </p>
                <div class="upload_atten">
                    <p></p>
                    <p>1.IPA</p>
                    <p>2.IPA </p>
                    <p>3.IPA-IPA</p>
                    <p>4.</p>
                </div>
            </div>
        </div>
        <div class="upload_content_container">
            <div class="upload_content">
                <div class="upload_content_tab">
                    <div class="clearfix">
                        <p
                            :class="['cursor','fl',{'active':tabIndex==0}]"
                            @click="changeTabIndex(0)"
                        >
                            <img src="../assets/img/file.png" alt />
                        </p>
                        <p
                            :class="['cursor','fl',{'active':tabIndex==1}]"
                            @click="changeTabIndex(1)"
                        >
                            <img src="../assets/img/file.png" alt />
                        </p>
                        <p
                            :class="['cursor','fl',{'active':tabIndex==2}]"
                            @click="changeTabIndex(2)"
                        >
                            <img src="../assets/img/file.png" alt />iTunes
                        </p>
                    </div>
                </div>
                <div class="upload_content_detail">
                    <!-- tab -->
                    <div class="tab_change">
                        <form
                            id="ajaxForm"
                            method="post"
                            action="https://sign.ddipa.com"
                            enctype="multipart/form-data"
                        >
                            <!--  -->
                            <div class="upload_file clearfix">
                                <p class="upload_detail_title fl">
                                    <span>*</span>
                                </p>
                                <div class="upload_selectfile cursor fl">
                                    <input
                                        type="text"
                                        name="sign_ipaname"
                                        class="input_name"
                                        placeholder="ipa"
                                        v-model="ipaName"
                                    />
                                </div>
                            </div>
                            <div class="upload_file clearfix" v-if="tabIndex == 0">
                                <p class="upload_detail_title fl"></p>
                                <div class="upload_selectfile cursor fl">
                                    <p class="cursor" style="display:flex;align-items:center">
                                        <span class="btn" v-if="!inputDisabled">IPA</span>
                                        <span class="btn" v-else>IPA</span>
                                        <img
                                            v-if="showLoading"
                                            style="margin-left:10px;width:20px;height:20px;"
                                            src="../assets/img/loading.gif"
                                            alt
                                        />
                                        <span class="tips">IPA2G</span>
                                    </p>
                                    <input
                                        type="file"
                                        ref="uploadfile"
                                        name="sign_ipa"
                                        @change="uploadIPA"
                                        class="cursor input_file"
                                        :disabled="inputDisabled"
                                    />
                                </div>
                            </div>
                            <input type="text" name="sign_type" v-model="signType" v-show="false" />
                            <input
                                type="text"
                                name="sign_userid"
                                v-model="signUserid"
                                v-if="signType!=1"
                                v-show="false"
                            />
                        </form>

                        <div class="upload_file clearfix" v-if="tabIndex == 0">
                            <p class="upload_detail_title fl"></p>
                            <div class="upload_selectfile cursor fl">
                                <p class="cursor" style="display:flex;align-items:center">
                                    <span class="btn" v-if="!imguploadDisabled">IPA</span>
                                    <img :src="iconSrc" v-if="imguploadDisabled" alt />
                                    <span class="btn" v-if="imguploadDisabled">IPA</span>
                                    <img
                                        v-if="showLoading"
                                        style="margin-left:10px;width:20px;height:20px;"
                                        src="../assets/img/loading.gif"
                                        alt
                                    />
                                    <span class="tips">IPA</span>
                                </p>
                                <form id="imgupload">
                                    <input
                                        type="file"
                                        name="file"
                                        @change="uploadIcon"
                                        class="cursor input_file"
                                        :disabled="imguploadDisabled"
                                    />
                                </form>
                            </div>
                        </div>
                        <!--  -->
                        <div class="upload_file clearfix" v-if="tabIndex == 1">
                            <p class="upload_detail_title fl"></p>
                            <div class="upload_selectfile fl">
                                <input
                                    type="text"
                                    class="lianjie"
                                    v-model="lianjieUrl"
                                    placeholder=""
                                />
                            </div>
                        </div>
                        <!-- iTunes -->
                        <div class="upload_file clearfix" v-if="tabIndex == 2">
                            <p class="upload_detail_title fl">iTunes</p>
                            <div class="upload_selectfile fl">
                                <input
                                    type="text"
                                    class="lianjie"
                                    v-model="itunesUrl"
                                    placeholder="iTunes"
                                />
                            </div>
                        </div>
                    </div>
                    <!--  -->
                    <div class="upload_wrap">
                        <!--  -->
                        <div class="upload_file clearfix">
                            <p class="upload_detail_title fl">
                                <span>*</span>
                            </p>
                            <div class="upload_selectfile fl">
                                <input
                                    type="text"
                                    class="lianjie"
                                    placeholder="QQ"
                                    v-model="qq"
                                />
                            </div>
                            <div class="upload_detail_tips fr">IPA</div>
                        </div>
                        <!--  -->
                        <div class="upload_file clearfix">
                            <p class="upload_detail_title fl">
                                <span>*</span>
                            </p>
                            <div class="upload_selectfile fl">
                                <textarea
                                    type="text"
                                    class="lianjie teatarea"
                                    placeholder="IPA60"
                                    v-model="describes"
                                ></textarea>
                            </div>
                            <div class="upload_detail_tips fr">IPAIPA</div>
                        </div>
                        <!--  -->
                        <div class="upload_file clearfix">
                            <p class="upload_detail_title fl"></p>
                            <div class="upload_selectfile fl">
                                <selectCom :data="multiOpenList" :value.sync="isMulti"></selectCom>
                            </div>
                            <div class="upload_detail_tips fr">
                                IPA
                                <span></span>
                            </div>
                        </div>
                        <!--  -->
                        <div class="upload_file clearfix">
                            <p class="upload_detail_title fl"></p>
                            <div class="upload_selectfile fl">
                                <selectCom :data="weixinLoginList" :value.sync="isWeixin"></selectCom>
                            </div>
                            <div class="upload_detail_tips fr">
                                IPAIPA
                                <span></span>
                            </div>
                        </div>
                        <!--  -->
                        <div class="upload_file clearfix">
                            <p class="upload_detail_title fl"></p>
                            <div class="upload_selectfile fl">
                                <selectCom :data="tuiguangGameList" :value.sync="isTuiguang"></selectCom>
                            </div>
                            <div class="upload_detail_tips fr">
                                ABAB
                                <span></span>
                            </div>
                        </div>
                        <!--  -->
                        <div class="upload_file clearfix">
                            <p class="upload_detail_title fl"></p>
                            <div class="upload_selectfile fl">
                                <selectCom :data="autoFahuoList" :value.sync="isAutoFahuo"></selectCom>
                            </div>
                            <div class="upload_detail_tips fr">
                                IPA/
                                <span></span>
                            </div>
                        </div>
                        <!-- -->
                        <div class="upload_file clearfix">
                            <p class="upload_detail_title fl"></p>
                            <div class="upload_selectfile fl">
                                <selectCom :data="autoshangjiaList" :value.sync="isAutoShangjia"></selectCom>
                            </div>
                            <div class="upload_detail_tips fr">IPA</div>
                        </div>
                        <!-- AppStore-->
                        <div class="upload_file clearfix">
                            <p class="upload_detail_title fl">AppStore</p>
                            <div class="upload_selectfile fl">
                                <div class="select_wrap clearfix">
                                    <div
                                        v-for="(ele,index) in appstoreFree"
                                        :key="index"
                                        class="select_option cursor fl"
                                        @click="isFree = ele.id"
                                    >
                                        <div class="circle fl">
                                            <div class="color_circle" v-show="ele.id == isFree"></div>
                                        </div>
                                        <p class="fl">{{ele.name}}</p>
                                    </div>
                                </div>
                                <input
                                    type="text"
                                    class="lianjie"
                                    placeholder="AppStore"
                                    v-show="!isFree"
                                />
                            </div>
                            <div class="upload_detail_tips fr">
                                IPAAppStore
                                <span></span>
                            </div>
                        </div>
                        <!-- -->
                        <div class="upload_file clearfix">
                            <p class="upload_detail_title fl">
                                <span>*</span>
                            </p>
                            <div class="upload_selectfile fl">
                                <commonSelect
                                    :list="appType"
                                    :value.sync="appTypeValue"
                                    width="120"
                                    height="40px"
                                ></commonSelect>
                            </div>
                        </div>
                        <!-- -->
                        <div class="upload_file clearfix">
                            <p class="upload_detail_title fl"></p>
                            <div class="upload_selectfile fl">
                                <div class="select_wrap">
                                    <div
                                        v-for="(ele,index) in jiamiTypeList"
                                        :key="index"
                                        class="select_option cursor clearfix"
                                        @click="jiamiType = ele.id"
                                    >
                                        <div
                                            class="fl clearfix"
                                            style="width:150px;margin-right:30px;"
                                        >
                                            <div class="circle fl">
                                                <div
                                                    class="color_circle"
                                                    v-show="ele.id == jiamiType"
                                                ></div>
                                            </div>
                                            <p class="fl">{{ele.name}}</p>
                                        </div>
                                        <span class="fl" style="color:#999999">{{ele.tip}}</span>
                                    </div>
                                </div>
                                <!-- <div class="select_shangchuanma">
                                    <p class="cursor"></p>
                                    <input type="file" class="cursor">
                                </div>-->
                            </div>
                            <div
                                class="upload_detail_tips fr"
                            >_IPA1_IPA5</div>
                        </div>
                        <!-- IPA -->
                        <div class="upload_file clearfix">
                            <p class="upload_detail_title fl">
                                <span>*</span>IPA
                            </p>
                            <div class="upload_selectfile fl">
                                <input
                                    type="text"
                                    class="lianjie"
                                    placeholder="IPA,5"
                                    v-model="IPAPrice"
                                />
                                <span style="margin-left:20px;"></span>
                            </div>
                        </div>
                        <!-- IPA -->
                        <div class="upload_file clearfix">
                            <p class="upload_detail_title fl">
                                <!-- <span>*</span>IPA -->
                            </p>
                            <div class="upload_selectfile fl">
                                <button class="submit_btn" @click="submitData"></button>
                                <button class="back_btn"></button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="bottom_content">
            <p class="bottom_name">IPA</p>
            <p class="bottom_copyright">Copyright  IPA</p>
        </div>
    </div>
</template>
<script>
import selectCom from "../components/selectCom";
export default {
    components: {
        selectCom
    },
    data() {
        return {
            iconSrc: "",
            imguploadDisabled: false,
            tabIndex: 0,
            // 
            ipaData: [],
            multiOpenList: [
                {
                    name: "",
                    id: "0"
                },
                {
                    name: "",
                    id: "1"
                }
            ],
            isMulti: "0",
            // 
            weixinLoginList: [
                {
                    name: "",
                    id: "0"
                },
                {
                    name: "",
                    id: "1"
                }
            ],
            isWeixin: "0",
            // 
            tuiguangGameList: [
                {
                    name: "",
                    id: "0"
                },
                {
                    name: "",
                    id: "1"
                }
            ],
            isTuiguang: "0",
            // 
            autoFahuoList: [
                {
                    name: "",
                    id: "0"
                },
                {
                    name: "",
                    id: "1"
                }
            ],
            isAutoFahuo: "0",
            // 
            autoshangjiaList: [
                {
                    name: "",
                    id: "0"
                },
                {
                    name: "",
                    id: "1"
                }
            ],
            isAutoShangjia: "0",
            appstoreFree: [
                {
                    name: "",
                    id: "0"
                },
                {
                    name: "",
                    id: "1"
                }
            ],
            isFree: "0",
            jiamiTypeList: [
                {
                    name: "",
                    id: "0",
                    tip: ""
                },
                {
                    name: "_",
                    id: "1",
                    tip: ""
                },
                {
                    name: "_",
                    id: "2",
                    tip: ""
                }
            ],
            jiamiType: 0,
            appType: [
                {
                    name: "",
                    id: "0"
                },
                {
                    name: "",
                    id: "1"
                }
                // {
                //     name: "",
                //     id: 3
                // }
            ],
            appTypeValue: {
                name: "",
                id: "0"
            },
            IPAPrice: "1",
            signType: 1,
            signUserid: 1,
            showLoading: false,
            ipaName: "",
            qq: "",
            describes: "",
            uploadSuccess: false,
            inputDisabled: false,
            uploadUrl: "",
            lianjieUrl: "",
            itunesUrl: ""
        };
    },
    methods: {
        uploadIcon(e) {
            // base64
            // var reader = new FileReader();
            // reader.readAsDataURL(e.target.files[0]); //  base64
            // obj.name = e.target.files[0].name;

            // formdata
            var form = document.getElementById("imgupload");
            console.log(form);
            var formData = new FormData(form);
            console.log(formData);
            this.$axios
                .post(
                    "https://www.ddipa.com:9090/DDApi/PhotoUpload/upload",
                    formData
                )
                .then(res => {
                    console.log(res);
                    if (res.data.code == 200) {
                        this.imguploadDisabled = true;
                        this.iconSrc = res.data.data;
                    }
                });
            // uploadImg(formData).then(res => {
            //     obj.url = res.data.url;
            //     this.receiptImgList.push(obj);
            // });
        },
        changeTabIndex(index) {
            this.tabIndex = index;
        },
        getData() {
            this.dataEdit = [];
            this.$axios
                .post(
                    "https://www.ddipa.com:9090/DDApi/template/queryOIPA",
                    this.$qs.stringify({ id: this.$route.query.id })
                )
                .then(res => {
                    if (res.status == 200) {
                        this.path = res.data.data.path;
                        this.ipaName = res.data.data.name;
                        this.qq = res.data.data.qq;
                        this.describes = res.data.data.describes;
                        this.isMulti = res.data.data.moreopen;
                        this.isWeixin = res.data.data.vw;
                        this.isTuiguang = res.data.data.popularize;
                        this.isFree = res.data.data.free;
                        this.isAutoFahuo = res.data.data.send;
                        this.isAutoShangjia = res.data.data.upper;
                        this.appTypeValue.id = res.data.data.type;
                        this.jiamiType = res.data.data.encryption;
                        this.IPAPrice = res.data.data.price;
                        this.tabIndex = res.data.data.uploadmode;
                        if (this.tabIndex == 0 || !this.tabIndex) {
                            this.inputDisabled = true;
                            this.uploadSuccess = true;
                            this.uploadUrl = res.data.data.path;
                        } else if (this.tabIndex == 1) {
                            this.lianjieUrl = res.data.data.path;
                        } else {
                            this.itunesUrl = res.data.data.path;
                        }
                    }
                });
        },
        uploadIPA(e) {
            var that = this;
            that.uploadSuccess = false;
            that.showLoading = true;
            $("#ajaxForm").ajaxSubmit(function(res) {
                that.showLoading = false;
                console.log("res+++");
                console.log(JSON.parse(res));
                that.uploadSuccess = true;
                that.uploadUrl = JSON.parse(res).oss.plist.url;
                alert("");
            });
        },
        submitData() {
            console.log(this.tabIndex);
            var url;
            if (this.tabIndex == 0) {
                url = this.uploadUrl;
            } else if (this.tabIndex == 1) {
                url = this.lianjieUrl;
            } else {
                url = this.itunesUrl;
            }
            if (!this.uploadSuccess && this.tabIndex == 0) {
                alert("");
                return;
            }

            if (!url) {
                alert("");
                return;
            } else if (!this.ipaName) {
                alert("ipa");
                return;
            } else if (!this.qq) {
                alert("QQ");
                return;
            } else if (!this.describes) {
                alert("");
                return;
            }
            var data = {
                path: url,
                photo: this.iconSrc,
                name: this.ipaName,
                authorid: "1",
                qq: this.qq,
                describes: this.describes,
                moreopen: this.isMulti,
                vw: this.isWeixin,
                popularize: this.isTuiguang,
                free: this.isFree,
                send: this.isAutoFahuo,
                upper: this.isAutoShangjia,
                type: this.appTypeValue.id,
                encryption: this.jiamiType,
                // 
                code: ("" + new Date().getTime()).substring(7, 13),
                // app
                state: "0",
                // 
                price: this.IPAPrice,
                uploadcode: this.$route.query.uploadcode
                    ? this.$route.query.uploadcode
                    : "",
                uploadmode: "" + this.tabIndex
            };
            if (this.$route.query.id) {
                data.id = this.$route.query.id;
                this.$axios
                    .post(
                        "https://www.ddipa.com:9090/DDApi/updateTemplate/updateIPA",
                        this.$qs.stringify(data)
                    )
                    .then(res => {
                        alert(res.data.message);
                        this.getData();
                    });
            } else {
                this.$axios
                    .post(
                        "https://www.ddipa.com:9090/DDApi/insetTemplate/insertIPA",
                        this.$qs.stringify(data)
                    )
                    .then(res => {
                        alert(res.data.message);
                        // this.getData();
                    });
            }
        }
    },
    watch: {
        jiamiType() {
            if (this.jiamiType) {
                // this.IPAPrice = 1;
            }
        }
    },
    created() {
        if (this.$route.query.id) {
            this.getData();
        }
    }
};
</script>
<style lang="scss" scoped>
.upload_container {
    .upload_title_container {
        width: 100%;
        min-width: 1200px;
        background: #fff;
        border-top: 1px solid #f1f1f1;
        .upload_title {
            width: 1200px;
            margin: 0 auto;
            padding: 20px 18px 0;
            .upload_menu {
                font-size: 18px;
                color: rgba(51, 51, 51, 1);
            }
            .upload_des {
                margin-top: 15px;
                font-size: 16px;
                color: rgba(51, 51, 51, 1);
                span {
                    margin-left: 30px;
                    color: #ec2828;
                }
            }
            .upload_atten {
                background: #d2e5f8;
                padding: 10px 18px;
                margin-top: 20px;
                color: #ec2828;
                font-size: 14px;
                p {
                    line-height: 25px;
                }
            }
        }
    }
    .upload_content_container {
        width: 100%;
        min-width: 1200px;
        background: #fff;
        // border-top: 1px solid #f1f1f1;
        .upload_content {
            width: 1200px;
            margin: 0 auto;
            padding: 20px 18px 50px;
            .upload_content_tab {
                div {
                    width: 481px;
                    border: 1px solid #e6e6e6;
                    border-right: 0;
                }
                p {
                    width: 160px;
                    height: 40px;
                    line-height: 40px;
                    font-size: 14px;
                    text-align: center;
                    color: #4d4d4d;
                    border-right: 1px solid #e6e6e6;
                    img {
                        width: 24px;
                        vertical-align: -5px;
                        margin-right: 5px;
                    }
                }
                p.active {
                    color: #fff;
                    background: #4ba3f3;
                }
            }
            .upload_content_detail {
                // padding-top: 30px;
                .upload_file {
                    margin-top: 30px;
                }
                .upload_detail_title {
                    width: 255px;
                    height: 40px;
                    text-align: right;
                    margin-right: 25px;
                    font-size: 14px;
                    color: #4d4d4d;
                    line-height: 40px;
                    span {
                        color: #ec2828;
                        font-size: 16px;
                        margin-right: 5px;
                    }
                }
                .upload_selectfile {
                    position: relative;
                    .select_wrap {
                        .select_option {
                            line-height: 40px;
                            font-size: 14px;
                            color: #4d4d4d;
                            margin-right: 30px;
                            position: relative;
                            .circle {
                                width: 20px;
                                height: 20px;
                                background: rgba(255, 255, 255, 1);
                                border: 1px solid rgba(75, 163, 243, 1);
                                border-radius: 50%;
                                margin-top: 10px;
                                margin-right: 5px;
                                position: relative;
                                .color_circle {
                                    width: 12px;
                                    height: 12px;
                                    background: rgba(75, 163, 243, 1);
                                    border-radius: 50%;
                                    position: absolute;
                                    top: 50%;
                                    margin-top: -6px;
                                    left: 50%;
                                    margin-left: -6px;
                                }
                            }
                        }
                    }
                    .select_shangchuanma {
                        position: relative;
                        p {
                            width: 150px;
                            height: 40px;
                            line-height: 40px;
                            text-align: center;
                            color: #fff;
                            font-size: 14px;
                            background: rgba(75, 163, 243, 1);
                            border-radius: 5px;
                            margin-top: 10px;
                        }
                        input {
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 150px;
                            height: 40px;
                            opacity: 0;
                        }
                    }
                    button {
                        outline: 0;
                        border: 0;
                        width: 120px;
                        height: 40px;
                        text-align: center;
                        line-height: 40px;
                        font-size: 14px;
                        border: 1px solid rgba(75, 163, 243, 1);
                    }
                    .submit_btn {
                        color: #fff;
                        background: rgba(75, 163, 243, 1);
                        margin-right: 60px;
                    }
                    .back_btn {
                        color: #4ba3f3;
                        background: #fff;
                    }
                    p {
                        span {
                            display: inline-block;
                            font-size: 14px;
                            line-height: 40px;
                        }
                        span.btn {
                            width: 150px;
                            background: #4ba3f3;
                            text-align: center;
                            color: #fff;
                        }
                        span.tips {
                            margin-left: 25px;
                            color: #999999;
                        }
                    }
                    .input_file {
                        position: absolute;
                        top: 0;
                        left: 0;
                        border: 0;
                        outline: 0;
                        width: 150px;
                        height: 40px;
                        // visibility: hidden;
                        opacity: 0;
                    }
                    .input_name {
                        width: 320px;
                        height: 40px;
                        background: white;
                        border: 1px solid #e6e6e6;
                        border-radius: 5px;
                        padding: 0 18px;
                        font-size: 14px;
                        color: #4d4d4d;
                    }
                    .lianjie {
                        width: 320px;
